import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom';

import styled from 'styled-components';

import Intro from './components/Intro';
import Layout from './components/Layout';
import EPub from './components/EPub';

import logo from './img/logo_biale.png';

import './App.css';

import {
	HeaderContainer,
	HeaderLogoContainer,
	HeaderLogo,
	HeaderLinks,
	HeaderButton
} from './styled/App';

class App extends Component {
  render() {
    return (
    	<div className="App">
    	<Router>
    		<div>
     		<HeaderContainer>
     			<HeaderLogoContainer>
     				<Link to="/">
     					<HeaderLogo src={logo} alt="Szortal logo"/>
     				</Link>
     			</HeaderLogoContainer>
     			<HeaderLinks>
     				<HeaderButton>
     					<Link to="/layout">Layout</Link>
     				</HeaderButton>
     				<HeaderButton>
     					<Link to="/epub">EPub</Link>
     				</HeaderButton>
     			</HeaderLinks>
     		</HeaderContainer>

     		<Route exact path={process.env.PUBLIC_URL + '/'} component={Intro} />
          	<Route path={process.env.PUBLIC_URL + '/layout'} component={Layout} />
          	<Route path={process.env.PUBLIC_URL + '/epub'} component={EPub} />

     		</div>
     	</Router>
     	</div>
    );
  }
}

export default App;


